}

.free-lesson {
  max-width: 683px;
  width: 100%;
  padding: 56px 103px 40px 103px;
  font-style: normal;
  color: #ffffff;
}
@media (max-width: 1199.5px) {
  .free-lesson {
    padding: 54px 163px 53px 133px;
    max-width: none;
    width: 100%;
  }
}
@media (max-width: 767.5px) {
  .free-lesson {
    padding: 58px 15px 54.5px 15px;
  }
}

.free-lesson h3 {
  margin: 0 0 11px 0;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-weight: 800;
  line-height: 156%;
  letter-spacing: 1.32px;
}
@media (max-width: 1199.5px) {
  .free-lesson h3 {
    font-weight: 700;
    margin: 0 0 21px 0;
    font-size: 42px;
    letter-spacing: 1.26px;
  }
}
@media (max-width: 767.5px) {
  .free-lesson h3 {
    margin-bottom: 22px;
    font-size: 32px;
    letter-spacing: 0.7px;
  }
}

.free-lesson form {
  display: flex;
  flex-direction: column;
  gap: 26px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #ffffff;
  font-size: 14px;
  line-height: 1;
}
@media (max-width: 1199.5px) {
  .free-lesson form {
    column-gap: 12.7%;
    row-gap: 25px;
  }
}
@media (max-width: 767.5px) {
  .free-lesson form {
    row-gap: 26px;
    width: 100%;
  }
}

.free-lesson__wrapper {
  display: flex;
  column-gap: 30px;
}
@media (max-width: 1199.5px) {
  .free-lesson__wrapper {
    column-gap: 60px;
  }
}
@media (max-width: 767.5px) {
  .free-lesson__wrapper {
    flex-direction: column;
    row-gap: 20px;
  }
}

.slider-row__svg-prev {
  position: absolute;
  content: "";
  cursor: pointer;
}
@media (max-width: 767.5px) {
  .slider-row__svg-prev {
    width: 32px;
    height: 39px;
  }
}

.slider-row__svg-next {
  position: absolute;
  content: "";
  transform: rotate(180deg);
  cursor: pointer;
}
@media (max-width: 767.5px) {
  .slider-row__svg-next {
    width: 32px;
    height: 39px;
  }
}

.slider-row__svg-circle {
  display: none;
}

.slider-row__svg-prev:hover .slider-row__svg-circle,
.slider-row__svg-next:hover .slider-row__svg-circle,
.slider-row__svg-prev:focus .slider-row__svg-circle,
.slider-row__svg-next:focus .slider-row__svg-circle {
  display: block;
}

.slider-row__svg-prev:active .slider-row__svg-circle,
.slider-row__svg-next:active .slider-row__svg-circle {
  display: block;
  fill: #102654;
}

.slider-row__svg-prev:active .slider-row__svg-path,
.slider-row__svg-next:active .slider-row__svg-path {
  display: block;
  stroke: #ffffff;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  display: none;
}

